.App-parameters-listWrapper {
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-negative: 1;
      flex-shrink: 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.App-parameters-list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: baseline;
      align-items: baseline;
  -ms-flex-line-pack: start;
      align-content: flex-start;
  -ms-flex-negative: 1;
      flex-shrink: 1;
}

@media (max-width: 1000px) {
  .App-parameters-list {
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
    -ms-flex-align: stretch;
        align-items: stretch;
    -ms-flex-positive: 1;
        flex-grow: 1;
  }

  .App-parameters-listWrapper {
    -ms-flex-align: stretch;
        align-items: stretch;
  }
}

.field {
  white-space: nowrap;
  margin: 0 0.5em 0.25em 0.5em;
}

.field label {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: start;
      align-items: flex-start;
}

.field-name {
  margin-right: 0.25em;
  font-size: large;
  min-width: 4em;
  text-align: right;
}

input[type="text"].field-value, textarea.field-value {
  width: 30em;
  -ms-flex-positive: 1;
      flex-grow: 1;
  border: solid #a9a9a9 1px;
  padding-left: 0.3em;
}

textarea.field-value {
  height: 3.25em;
}

@media (max-width: 1000px) {
  input[type="text"].field-value, textarea.field-value {
    width: unset;
  }
}


