.App-processList {
  -ms-flex-positive: 1;
      flex-grow: 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: baseline;
      align-items: baseline;
  -ms-flex-line-pack: start;
      align-content: flex-start;
}

.App-processList button.active {
  border-color: white;
}

.App-processes button.recording {
  -webkit-animation: 1250ms infinite alternate recording;
          animation: 1250ms infinite alternate recording;
}

@-webkit-keyframes recording {
  to { background-color: #faa; }
}

@keyframes recording {
  to { background-color: #faa; }
}

.App-processes button.recorded:not(.button_expanded) {
  -webkit-animation: 1250ms infinite alternate recorded;
          animation: 1250ms infinite alternate recorded;
}

@-webkit-keyframes recorded {
  to { color: #666; border-color: white;  }
}

@keyframes recorded {
  to { color: #666; border-color: white;  }
}


